import React from "react";
import { Table, Pagination } from "react-bootstrap";
import SpinnerComponent from "../utils/spinner";
import { truncate } from "../utils/_funcs";

class DepotCustomers extends React.Component {

    state = {
        spinner: false,
        currency_type: this.props.currency_type,
        customers: [],
        active: 1,
    }
    static getDerivedStateFromProps = (props, state) => {
        if (props.update_depot_series || props.update_depot_customer) {
            state.spinner = true
            return state
        } else {
            state.spinner = false
        }
        // let customers = update(props)
        state.customers = props.depot_customer.revenue
        state.currency_type = props.currency_type
        return state
    }


    render() {
        const currency_type = this.state.currency_type
        const customers = this.state.customers
        let items = [];
        const NUM_ORDERS = 8
        let active = this.state.active;
        const start_index = (active - 1) * NUM_ORDERS
        const end_index = (active * NUM_ORDERS)
        for (let number = 1; number <= Math.ceil(customers.length / NUM_ORDERS); number++) {
            items.push(
                <Pagination.Item key={number} active={number === active} onClick={() => this.setState({ active: number })}>
                    {number}
                </Pagination.Item>,
            );
        }

        return (
            <div className="customer-summary">
                <h6>Depot Customers</h6>
                {this.state.spinner ? <div style={{ height: 400, position: "relative" }}><SpinnerComponent spinner={this.state.spinner} /></div> :
                    <div>
                        <Table striped bordered hover size="sm">
                            <thead>
                                <tr>
                                    <th></th>
                                    <th>Name</th>
                                    <th>No Orders</th>
                                    <th>{currency_type === "Million" ? "Revenue (Millions)" : "Revenue (Thousands)"}</th>
                                    <th>{currency_type === "Million" ? "Quantity (Millions)" : "Quantity (Thousands)"}</th>
                                </tr>
                            </thead>
                            <tbody>
                                {customers.slice(start_index, end_index).map((customer, idx) =>
                                    <tr key={idx}>
                                        <td>{start_index + idx + 1}</td>
                                        <td>{customer.customer__name}</td>
                                        <td>{customer.count}</td>
                                        <td>{currency_type === "Million" ? truncate(customer.sum / 1000000) : truncate(customer.sum / 1000)}</td>
                                        <td>{currency_type === "Million" ? truncate(customer.quantity / 1000000) : truncate(customer.quantity / 1000)}</td>
                                    </tr>
                                )}


                            </tbody>
                        </Table>

                        <Pagination size="sm">{items}</Pagination>
                    </div>
                }
            </div>
        )
    }
}

export default DepotCustomers